/*
Пользовательский checkbox
<label class="custom-checkbox">
  <input type="checkbox" class="custom-checkbox__field" />
  <span class="custom-checkbox__content">мужской</span>
</label>

в scss задать
.custom-checkbox{
  @include custom-checkbox;
}
 */
